body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("/img/SpaceBackground.jpg");
  background-repeat: no-repeat;
}
#dinheiro {
  margin-top: 2vh;
  display: flex;
  justify-content: center;
  font-size: 30px;
  color: rgb(255, 230, 0);
}
#planetasContainer {
  height: 95vh;
  display: flex;
  flex-flow: column wrap;
  justify-content: space-around;
}
#wrap {
  width: 600px;
  font-size: 25px;
  display: flex;
  flex-flow: wrap column;
  margin: 2% 52% 0 25%;
}
#content {
  height: 27%;
  color: wheat;
}
#imagem {
  height: 93%;
}
#botaoPlaneta {
  border-color: #8B0000;
  background-color: #8B0000;
  border-radius: 50%;
  height: 150px;
  width: 150px;
  border-width: 20px;
}
#investir {
  border-color: rgb(235, 187, 98);
  border-width: 5px;
  background-color: rgb(107, 88, 52);
  height: 80px;
  width: 120px;
  color: wheat;
}
#middle {
  display: flex;
  justify-content: space-between;
  width: 80%;
  align-items: center;
  margin-top: 2%;
}
#footer {
  display: flex;
  justify-content: space-between;
  width: 60%;
  margin: 2% 0 0 16%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (max-width: 700px) {
  body {
    background-repeat: repeat;
  }
  #dinheiroPosition{
    display: flex;
    justify-content: center;
  }
  #dinheiro {
    position: fixed;
    margin-top: 1vh;
    font-size: 30px;
  }
  #middle {
    width: 100%;
  }

  #wrap {
    width: 100%;
    flex-wrap: nowrap;
    margin: 2% 0;
  }

  #planetasContainer {
    margin-top:500px;
    flex-wrap: nowrap;
  }
  #botaoPlaneta {
    height: 130px;
    width: 130px;
    border-width: 20px;
  }

  #content {
    margin: 4rem 0 2rem;
  }
}
